.center {
    margin: 0 auto;
  }
  .profile {
    display: flex;
    flex-direction: column;
    align-content: center;
    width: 90%;
    max-width: 500px;
    box-shadow: 0px 12px 40px 9px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    padding: 20px;
    background-color: hsl(210, 10%, 98%);
    &_button {
      margin: 0 auto !important;
      margin-top: 20px !important;
      width: 100% !important;
      height: 40px !important;
      padding: 5px !important;
  
      display: flex;
      align-items: center;
      justify-content: center;
  
      color: #fff;
      background: #6c1bdb;
      font-size: 1rem;
      font-weight: bold;
      cursor: pointer;
      border: none;
      border-radius: 4px;
      text-decoration: none;
      margin: 0 auto;
      &:disabled {
        margin: 0 auto;
        margin-top: 30px;
        width: 90%;
        height: 40px;
        padding: 5px;
      }
    }
    &_header {
      text-align: center;
      font-weight: 700;
      margin-top: 0;
      font-size: 35px;
    }
    &_text {
      font-size: 17px;
    }
  }