.pagesContainer {
    padding: 0;
    display: flex;
    justify-content: center;
    list-style: none;
    width: 100%;
    height: 24px;
    &_Dark {
      padding: 0;
      display: flex;
      justify-content: center;
      list-style: none;
      width: 100%;
      height: 24px;
      background-color: #313037;
    }
  }
  .pagesContainer1 {
    display: none;
  }
  .pageNumber {
    display: block !important;
    font-weight: 600 !important;
    font-size: 16px !important;
    line-height: 24px !important;
    color: #313037 !important;
    width: 50px !important;
    height: 50px !important;
    a {
      cursor: pointer;
      &:hover {
        color: blue !important;
      }
    }
  }
  .pageNumber_Dark {
    display: block !important;
    color: #fff !important;
    font-weight: 600 !important;
    font-size: 16px !important;
    line-height: 24px !important;
    width: 50px !important;
    height: 50px !important;
  }
  .activePageNumber {
    color: coral !important;
  }
  .nextButton {
    margin-left: auto;
    color: #8d8e97;
    position: relative;
    svg > path {
      fill: #8d8e97;
    }
  }
  .prevButton {
    margin-right: auto;
    position: relative;
    color: #8d8e97;
  }
  .availableToClickButton {
    color: green;
  }
  
