@import "src/assets/colors.scss";
.Post {
    background-color: #fff;
    margin-bottom: 75px;
    border-bottom: 1px solid rgba(49, 48, 55, 0.1);
    &_Dark {
      background-color: #313037;
    }
    &_container {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      height: 100%;
      width: 1120px;
      margin: 0 auto;
      &_buttonAndIDWrap {
        margin-top: 72px;
        margin-right: auto;
        display: flex;
      }
      &_titleWrap {
        margin-top: 32px;
        font-weight: 700;
        font-size: 50px;
        line-height: 80px;
        color: #313037;
        text-align: center;
        &_Dark {
          color: #fff;
        }
      }
      &_imgWrap {
        width: 992px;
        height: 518px;
        margin: 0 auto;
        &_img {
          margin-top: 42px;
          width: 100%;
          height: 100%;
          border-radius: 10%;
        }
      }
      &_textWrap {
        margin-top: 58px;
        max-width: 922px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 48px;
        margin-bottom: 48px;
        &_text {
          font-weight: 400;
          font-size: 18px;
          line-height: 32px;
          color: #313037;
          &_Dark {
            color: #fff;
          }
        }
      }
      &_buttonsWrap {
        margin-bottom: 72px;
        width: 922px;
        display: flex;
        margin: 0;
        padding: 0;
        justify-content: space-between;
        margin-bottom: 45px;
        &_rightSide {
          display: flex;
        }
      }
      &_Dark {
        background-color: #313037;
      }
    }
  }
  .linkWrap {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #313037;
    text-decoration: none;
    &_Dark {
      color: #fff;
    }
  }
  .idWrap {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #8d8e97;
    margin-left: 8px;
  }
  
  .ButtonIconFacebook,
  .ButtonIconTvitter {
    margin-right: 6px;
  }
  
  .Post_container_buttonsWrap_buttons {
    a {
      width: 88px;
      height: 56px;
      display: flex;
      align-items: center;
      cursor: pointer;
      justify-content: center;
    }
    width: 88px;
    height: 56px;
    background: rgba(49, 48, 55, 0.1);
    border-radius: 4px;
    border: none;
    margin-right: 6px;
    display: flex;
    align-items: center;
    cursor: pointer;
    justify-content: center;
    &:hover {
      background: #912ef2;
    }
    &:disabled {
      background: rgba(49, 48, 55, 0.1) !important;
      border-radius: 2px;
      color: rgba(49, 48, 55, 0.5) !important;
    }
    &_Dark {
      svg > path {
        stroke: white;
      }
      background: rgba(255, 255, 255, 0.3);
      &:disabled {
        background: rgba(255, 255, 255, 0.1);
      }
    }
  }