@import "colors";
@mixin button{
    border-radius: 2px;
    padding: 16px 32px;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    box-sizing: border-box;
    border: 1px solid transparent;
    background: #6c1bdb;
    &:hover{
        cursor: pointer;
    }
}
@mixin title {
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: $secondary;
    margin-bottom: 8px;
  }